export default {
	data() {
		return {
			getimg:this.$getimg,
			price:'',
			my_order:'',
			mr_address:'',
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			addressData: {}
		}
	},
	onLoad(option){
		this.get_my_order(option.id)
		// this.get_moren_addr()
		//商品数据
		//let data = JSON.parse(option.data);
		//console.log(data);
	},
	methods: {
		// get_moren_addr(){
		// 	this.$api.http.get('address/get_default_address').then(res=>{
		// 		this.mr_address = res.data
		// 		console.log(res.data)
		// 	})
		// }, 
		get_my_order(id){
			this.$api.http.get('order/user/get_order_one',{id:id}).then(res=>{
				this.my_order = res.data
				console.log(res.data)
				let a=0
				for (let v in res.data.ordergoods) {
					a += res.data.ordergoods[v].price * res.data.ordergoods[v].num
				}
				this.price = a.toFixed(2) * 1
				uni.setStorageSync('price',this.price)
				console.log(this.price)
			})
		},
		jump_pro(id){
			uni.navigateTo({
				url:'/pages/extend-view/productDetail/productDetail?id='+id
			})
		},
		jump_torefund(id){
			uni.navigateTo({
				url:'./refund/refund?id='+id
			})
		},
		jump_tograde(){
			let obj = {
				id:this.my_order.order_id,
				data:this.my_order.ordergoods
			}
			uni.setStorageSync('grade_pro',obj)
			uni.navigateTo({
				url:'./grade/grade'
			})
		}
	},
	onPullDownRefresh() {
		
	
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
